import {
    TimelineMax,
    Power3,
    Back
} from 'gsap/all';
import SplitText from "gsap/SplitText";
import * as THREE from 'three'
import {
    fragment,
    vertex
} from './shaders';


export default class Slider {
    /**
     * Creation du slider
     * @param {HTMLElement} element Element qui sera transformer
     * @param {*} options Objet d'options
     * @param {boolean} [options.infinite=true] Défilement infini
     */
    constructor(element, options = {}) {
        this.element = document.querySelector(element)
        this.children = [].slice.call(this.element.children)
        this.options = Object.assign({}, {
            infinite: true,
            slideVisible: 1,
            slideToScroll: 1,
        }, options)
        this.current = 0
        this.offset = 0
        this.nextElement


        // DOM
        this.splitCity = new SplitText('.title', {
            type: 'lines, words',
            linesClass: 'line',
            wordsClass: 'word'
        })
        this.splitCountry = new SplitText('.title + p', {
            type: 'lines, words',
            linesClass: 'line',
            wordsClass: 'word'
        })


        // SET SLIDER
        this.setStyle()
        this.setCanvas()
        this.setPostion()
        this.navigation()
    }

    navigation() {
        const nextButton = document.querySelector('.slider__navigation--next')
        const prevButton = document.querySelector('.slider__navigation--previous')
        nextButton.addEventListener('click', this.next.bind(this))
        prevButton.addEventListener('click', this.prev.bind(this))
    }

    next(e) {
        e.preventDefault()
        this.goToItem(this.current + this.options.slideToScroll, 'next')
    }

    prev(e) {
        e.preventDefault()
        this.goToItem(this.current - this.options.slideToScroll, 'prev')
    }

    /**
     * déplace le carousel vers l'element ciblé
     * @param {number} index position du slider
     * @param {string} direction direction du slider
     * @return {*} nothing
     */
    goToItem(index, direction) {
        const currentSlide = this.children[this.current]
        const currentCity = currentSlide.querySelector('.title .word')
        const currentCountry = currentSlide.querySelector('.title + p .word')

        if (index < 0) {
            index = this.children.length - this.options.slideVisible
        } else if (index >= this.children.length || (this.children[this.current + this.options.slideVisible] === undefined && index > this.current)) {
            index = 0
        }
        this.current = index
        this.nextElement = this.current == 0 ?
            this.children.length - 1 :
            (this.current == this.children.length - 1 ?
                0 :
                this.current + 1)
        const nextSlide = this.children[index]
        const nextCity = nextSlide.querySelector('.title .word')
        const nextCountry = nextSlide.querySelector('.title + p .word')
        const translateOut = direction == 'next' ? '-100%' : '100%'
        const translateIn = direction == 'next' ? '100%' : '-100%'
        const translateXout = direction == 'next' ? -window.innerWidth : window.innerWidth
        const translateXIn = direction == 'next' ? window.innerWidth : -window.innerWidth




        const tl = new TimelineMax({
            onComplete: () => {
                this.updatePosition()
            }
        })
        // OUT
        tl.to(currentCity, .4, {
            y: translateOut,
            ease: Power3.easeInOut
        }, '#start')
        tl.to(currentCountry, .4, {
            y: translateOut,
            ease: Power3.easeInOut
        }, '-=0.2')
        tl.to(currentSlide, .4, {
            scale: .6,
            ease: Back.easeOut.config(1.7)
        })
        tl.to(currentSlide, .6, {
            x: translateXout,
            ease: Power3.easeInOut,
            delay: .2
        })

        // IN
        tl.fromTo(nextSlide, .6, {
            opacity: 1,
            scale: .6,
            x: translateXIn,
            ease: Power3.easeInOut,
        }, {
            opacity: 1,
            x: 0
        }, '-=0.5')
        tl.to(nextSlide, .4, {
            scale: 1,
            ease: Back.easeOut.config(1.7),
        })
        tl.fromTo(nextCity, .4, {
            y: translateIn,
            ease: Power3.easeInOut
        }, {
            y: '0%'
        })
        tl.fromTo(nextCountry, .4, {
            y: translateIn,
            ease: Power3.easeInOut
        }, {
            y: '0%'
        }, '-=0.2')


    }

    setStyle() {
        this.children.map(child => {
            child.style.opacity = '0';
        })
        this.children[0].style.removeProperty('opacity')
    }

    setPostion() {
        document.querySelector('.slider__position__actif').innerHTML = this.current < 10 ? `0${this.current + 1}` : this.current + 1
        document.querySelector('.slider__position__total').innerHTML = this.children.length < 10 ? `0${this.children.length}` : this.children.length
    }

    updatePosition() {
        document.querySelector('.slider__position__actif').innerHTML = this.current < 10 ? `0${this.current + 1}` : this.current + 1
    }

    setCanvas() {
        const camera = new THREE.PerspectiveCamera(45, 1, 0.1, 10000);
        const fovInRadians = (camera.fov * Math.PI) / 180;
        // Camera aspect ratio is 1. The view width and height are equal.
        const viewSize = Math.abs(camera.position.z * Math.tan(fovInRadians / 2) * 2);
        const geometry = new THREE.PlaneBufferGeometry(viewSize * 1.5, viewSize, 60, 60)

        const material = new THREE.ShaderMaterial({
            uniforms: {
                // Progress of the effect
                u_progress: {
                    type: "f",
                    value: 0
                },
                // In which direction is the effect going
                u_direction: {
                    type: "f",
                    value: 1
                },
                u_waveIntensity: {
                    type: "f",
                    value: 0
                }
            },
            vertexShader: vertex,
            fragmentShader: fragment,
            side: THREE.DoubleSide
        });
    }


}